// LeaderInfo.tsx
import React from 'react';
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

interface LeaderInfoProps {
  state: string;
  headOfState: string;
  headOfStateImage: string | null;
  headOfGovernment: string;
  headOfGovernmentImage: string | null;
}

const LeaderInfo: React.FC<LeaderInfoProps> = ({ state, headOfState, headOfStateImage, headOfGovernment, headOfGovernmentImage }) => {
  return (
    <div className="leader-info fixed bottom-[740px] right-24 w-72 bg-opacity-20 rounded-xl p-4 mb-0 backdrop backdrop-blur-xl text-gray-50 shadow-sm overflow-hidden">
      <h2 className="text-xl text-black dark:text-green-400 font-semibold text-center mb-2">Head(s) of State</h2>
      <div className="flex justify-center space-x-4">
        {headOfStateImage && (
          <div className="flex items-center space-x-2 space-y-2">
            <Avatar>
              <AvatarImage src={headOfStateImage} alt={headOfState} />
              <AvatarFallback>{headOfState.charAt(0)}</AvatarFallback>
            </Avatar>
            <p className="text-sm text-black dark:text-white">{headOfState}</p>
          </div>
        )}
        {headOfGovernmentImage && (
          <div className="flex items-center space-x-2 space-y-2">
            <Avatar>
              <AvatarImage src={headOfGovernmentImage} alt={headOfGovernment} />
              <AvatarFallback>{headOfGovernment.charAt(0)}</AvatarFallback>
            </Avatar>
            <p className="text-sm text-black dark:text-white">{headOfGovernment}</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default LeaderInfo;
